<template>
  <div class="recommend" ref="recommend">
    <scroll ref="scroll" class="recommend-content" :data="newsHotList">
      <div>
        <div v-if='newsHotSlider.length' class="slider-wrapper">
          <slider>
            <div v-for="item in newsHotSlider" :key="item.linkUrl">
              <a :href="item.linkUrl">
                <img class="needsclick" @load="loadImage" :src="item.picUrl"/>
              </a>
            </div>
          </slider>
        </div>
        <div class="recommend-list">
          <ul>
            <li v-for="item in newsHotList" class="item" :key="item.newsId">
              <div class="icon">
                <img width="100" height="60" v-lazy="item.newsImage"/>
              </div>
              <div class="text">
                <div class="name" v-html="item.newsTitle"></div>
                <div class="desc" v-html="item.newsDesc"></div>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="loading-content" v-show="!newsHotList.length">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>

<script style="text/ecmascript-6">
import Loading from 'base/loading/loading'
import Slider from 'base/slider/slider'
import Scroll from 'base/scroll/scroll'
import { getNewsHotList, getNewsHotSlider } from 'api/newsHot'
export default {
  data () {
    return {
      newsHotSlider: [],
      newsHotList: []
    }
  },
  components: {
    Slider,
    Loading,
    Scroll
  },
  created () {
    this._getNewsHotSlider()
    this._getNewsHotList()
  },
  methods: {
    _getNewsHotList () {
      // this.newsHotList = getNewsHotList()
      console.log(getNewsHotList())
    },
    _getNewsHotSlider () {
      getNewsHotSlider().then((res, err) => {
        if (res.code === 0) {
          this.newsHotSlider = res.data.slider
        }
      })
    },
    loadImage () {
      if (!this.checkLoaded) {
        setTimeout(() => {
          this.$refs.scroll.refresh()
        }, 500)
        this.checkLoaded = true
      }
    }
  }
}
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/variable"
.recommend
  position: fixed
  width: 100%
  top: 88px
  bottom: 0
.recommend-content
  height: 100%
  overflow: hidden
.slider-wrapper
  position: relative
  width: 100%
  overflow: hidden
.item
  display: flex
  box-sizing: border-box
  align-items: center
  padding: 5px 5px 4px 5px
  border-bottom 1px solid #fff
.icon
  flex: 0 0 100px
  width: 100px
  padding-right: 5px
.text
  display: flex
  flex-direction: column
  width 100%
  height 60px
  flex: 1
  line-height: 18px
  overflow: hidden
  font-size: $font-size-medium
.name
  margin:  0 0 5px 0
  color: $color-text
.desc
  color: $color-text-d
  overflow hidden
.loading-container
  position: absolute
  width: 100%
  top: 50%
  transform: translateY(-50%)

</style>



