import Vue from 'vue'
import Router from 'vue-router'
import NewsHot from 'components/news-hot/news-hot'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/newsHot'
    },
    {
      path: '/newsHot',
      component: NewsHot
    }
  ]
})



